.footer {
  &__break {
    background-image: url(../images/footer.png);

    height: 200px ;

    background-size: cover;
    background-position: center;
    margin-bottom: 48px ;
  
    @include onTablet {
      height: 400px;
      margin-bottom: 56px;
    }
  
    @include onDesktop {
      height: 460px;
    }
  }
}
