.recommended {
  transition-duration: 1s;

  &__products {
    @include pageGrid;
    row-gap: 40px;
  }

  &__product {
    @include hover (transform, scale(1.1));

    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 2/-2;
    }

    @include onDesktop {
      grid-column: span 4;
    }
  }
}
